@import url("https://fonts.googleapis.com/css?family=Josefin+Sans&display=swap");
.timeline {
  display: flex;
  flex-direction: column;
  margin-left: 10%;
  z-index: 1; }
  .timeline::after {
    content: '';
    z-index: -1;
    background-color: wheat;
    position: absolute;
    left: 50%;
    width: 0.3rem;
    height: 75rem; }

.timeline-item {
  display: flex;
  flex-direction: column;
  position: relative;
  background-color: #faf9ec;
  box-shadow: 4px 4px 10px grey;
  border-radius: 8px;
  list-style-type: none;
  text-align: right;
  padding: 1rem;
  width: 25rem;
  max-width: 60%;
  cursor: pointer; }
  .timeline-item:nth-child(odd) {
    align-self: flex-end;
    text-align: left;
    margin-right: 10%;
    padding-left: 2rem;
    padding-right: 0; }
  .timeline-item:nth-child(odd) .timeline-item::after {
    right: auto;
    left: -7.5px; }

@media only screen and (max-width: 1023px) {
  .timeline-item {
    max-width: 100%; } }

@media only screen and (max-width: 800px) {
  .timeline-item,
  .timeline-item:nth-child(odd) {
    padding: 15px 7px;
    text-align: center;
    align-items: center; }
    .timeline-item a,
    .timeline-item:nth-child(odd) a {
      text-decoration: underline; } }
