@import url("https://fonts.googleapis.com/css?family=Josefin+Sans&display=swap");
.navbar {
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
  background-color: rgba(235, 235, 235, 0.3);
  margin-top: -1rem; }
  .navbar ul, .navbar li, .navbar a {
    grid-column: 1 / 4;
    display: inline-flex;
    flex-flow: row wrap;
    justify-content: space-evenly;
    list-style-type: none;
    text-decoration: none;
    font-family: "Josefin Sans", sans-serif;
    font-weight: bold;
    font-size: 1.3rem;
    color: #4d3d3d; }
  .navbar a:hover {
    color: wheat;
    text-shadow: 3px 1px 4px black;
    font-size: 1.4rem; }
  .navbar label, .navbar #hamb-btn {
    display: none; }

@media screen and (max-width: 700px) {
  .navbar ul {
    display: none; }
  .navbar li {
    border: 0.5px solid #8c8c8c;
    box-shadow: 2px 2px 5px black;
    background-color: #ffe6cc;
    max-width: 7rem;
    line-height: 4rem;
    margin-top: 1.5rem;
    margin-bottom: 1rem;
    padding: 0.5rem 0.5rem; }
  .navbar label {
    display: inline-block;
    background-color: transparent;
    pointer-events: none;
    margin-left: 0.5rem; }
  .navbar label span {
    display: inline-block;
    background-color: #333;
    border-radius: 3px;
    width: 35px;
    height: 5px;
    pointer-events: all;
    cursor: pointer;
    transition: 0.4s; }
    .navbar label span:nth-child(2) {
      display: flex;
      align-self: center;
      margin: 3px 0; }
    .navbar label span:nth-child(3) {
      display: flex;
      align-self: flex-end;
      margin: 7px 0; }
  .navbar input:checked ~ ul {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    background-color: rgba(140, 140, 140, 0.1);
    max-height: auto; }
  .navbar input:checked ~ label span:first-child {
    background-color: transparent; }
  .navbar input:checked ~ label span:nth-child(2) {
    transform: translateY(0) rotate(-45deg); }
  .navbar input:checked ~ label span:nth-child(3) {
    transform: translateY(0) rotate(45deg);
    margin: -8px 0; } }
